//Susy configuration
//Documentation http://susydocs.oddbird.net/en/latest/
$susy: ( 
	global-box-sizing: border-box, 
	columns: 12, 
	gutter-position: inside, 
	gutters: 1/4,
	debug:(
		image: hide, 
		color: rgba(0,0,255,0.5), 
		output: background, 
		toggle: top right,
	)
);

$img: "../img/"; //images path
$fonts: "../fonts/"; //fonts path

//Basic project styles
$mainFont: 'Lato', 'Arial', 'Helvetica', sans-serif;
$mainColor: #f9ba48;
$accentColor: #f9ba48;