@media only screen and (max-width: 767px) {
    .b-gallery__list {
        margin: 0 -5px;
    }
}

.b-gallery__info {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: scale(0.5) translateY(-50%);
    z-index: 2;
    opacity: 0;
    transition: all 0.3s;
    color: #fff;
    text-align: center;
}

.b-gallery__info .title {
    display: block;
    font-size: 18px;
    font-weight: 700;
}

@media only screen and (max-width: 479px) {
    .b-gallery__info .title {
        font-size: 14px;
    }
}

.b-gallery__info .time {
    display: block;
    font-size: 14px;
    font-style: italic;
}

@media only screen and (max-width: 479px) {
    .b-gallery__info .time {
        font-size: 10px;
    }
}

.b-gallery__info .icon-zoom {
    color: #2acb1e;
    font-size: 30px;
}

@media only screen and (max-width: 479px) {
    .b-gallery__info .icon-zoom {
        font-size: 20px;
    }
}

.b-gallery__item {
    display: block;
    position: relative;
    outline: none;
}

.b-gallery__item.is-video:hover .btn--play {
    background-color: #2acb1e;
}

.b-gallery__item:hover::before, .b-gallery__item.is-video::before {
    opacity: 1;
    transform: scale(1);
}

.b-gallery__item:hover .b-gallery__info, .b-gallery__item.is-video .b-gallery__info {
    opacity: 1;
    transform: scale(1) translateY(-50%);
}

.b-gallery__item::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.75);
    opacity: 0;
    z-index: 1;
    transform: scale(0.5);
    transition: all 0.3s;
}

.b-gallery__item::after {
    content: "";
    padding-bottom: 80.18867924528302%;
    display: block;
}

.b-gallery__item-wrap {
    width: 25%;
    float: left;
    padding: 0 15px;
    margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
    .b-gallery__item-wrap {
        padding: 0 5px;
        margin-bottom: 10px;
    }
}

.b-gallery__item-wrap.size-big {
    width: 50%;
}

.b-gallery__item-wrap.size-big .b-gallery__item::after {
    padding-bottom: 81.31868131868132%;
}

@media only screen and (max-width: 767px) {
    .b-gallery__item-wrap.size-big .b-gallery__item::after {
        padding-bottom: 80.7%;
    }
}

.b-gallery__item img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
}

.b-footer {
    min-height: 400px;
    position: relative;
}

.b-footer::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 50%;
    right: calc(50% + 150px);
    background: url("../img/footer-bg.jpg") no-repeat right top;
    z-index: -1;
}

@media only screen and (max-width: 959px) {
    .b-footer::after {
        left: 0;
        width: 335px;
    }
}

@media only screen and (max-width: 767px) {
    .b-footer::after {
        left: 0;
        right: 0;
        width: auto;
    }
}

.b-map {
    position: absolute;
    right: 0;
    bottom: 0;
    top: 0;
    left: 50%;
    left: calc(50% - 150px);
    z-index: 0;
}

@media only screen and (max-width: 959px) {
    .b-map {
        left: 335px;
    }
}

@media only screen and (max-width: 767px) {
    .b-map {
        display: block;
        position: static;
        width: 100%;
        height: 250px;
    }
}

.b-map__inner {
    width: 100%;
    height: 100%;
}

.b-contacts {
    display: table;
    max-width: 320px;
    padding: 55px 0;
    color: #fff;
    position: relative;
    z-index: 2;
}

@media only screen and (max-width: 767px) {
    .b-contacts {
        margin: 0 auto;
        padding: 20px 0;
    }
}

.b-contacts .b-title {
    margin-bottom: 18px;
    text-align: left;
}

.b-contacts__item {
    margin-bottom: 15px;
    font-size: 15px;
    font-weight: 600;
}

.b-contacts__item.type-address {
    font-size: 20px;
    font-weight: 700;
}

.b-contacts__item.type-address i {
    font-size: 23px;
}

.b-contacts__item i {
    display: inline-block;
    font-size: 20px;
    width: 30px;
    color: #2aca1e;
    vertical-align: -2px;
}

.b-contacts__bottom {
    margin: 30px 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.b-copyrights {
    color: #7b7b7b;
    font-family: Lato;
    font-size: 14px;
}

.b-social {
    cursor: default;
}

.b-social__link {
    display: inline-block;
    margin-right: 10px;
    text-decoration: none;
    color: #2acb1e;
    font-size: 40px;
    background-color: #fff;
    border-radius: 50%;
    height: 37px;
}

.b-social__link i {
    display: block;
    margin-top: -2px;
    vertical-align: top;
    line-height: 1;
}

.b-social__link:last-of-type {
    margin-right: 0;
}

.b-developers {
    font-family: Lato;
    font-size: 14px;
    color: #7b7b7b;
}

.b-developers a {
    color: #29ba1e;
    border-bottom: 1px solid transparent;
    text-decoration: none;
    transition: all 0.3s;
}

.b-developers a:hover {
    border-color: #29ba1e;
}

.popup__title {
    margin-bottom: 20px;
    color: #000;
    font-size: 26px;
    text-align: center;
}

@media only screen and (max-width: 479px) {
    .popup__title {
        font-size: 22px;
    }
}

.popup-callback {
    max-width: 375px;
    padding: 30px 40px 40px;
    margin: 0 auto;
    position: relative;
    background-color: #fff;
}

@media only screen and (max-width: 479px) {
    .popup-callback {
        padding: 30px 20px 30px;
    }
}

.popup-callback .btn--alt {
    margin-top: 10px;
    width: 100%;
}

.popup-callback .btn--alt::after {
    background-position: 35px center;
}

.popup-callback .btn--alt::before {
    content: none;
}

.popup-thanks {
    padding: 60px 40px;
    max-width: 375px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    background-color: #fff;
}

.popup-thanks img {
    display: block;
    margin: 0 auto 15px;
    width: 109px;
    height: 109px;
}

.popup-thanks__title {
    margin-bottom: 10px;
    color: #000;
    font-size: 30px;
    font-weight: 500;
}

.popup-thanks__text {
    color: #000;
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
}

.popup-video {
    position: relative;
    margin: 0 auto;
    max-width: 1200px;
}

.popup-video video {
    max-width: 100%;
    height: auto;
}
